<template>
  <div>
    <div class="border border-dark pl-4 pr-4 ml-5">
      <b-row>
        <div class="title-fs-2">{{ $t("SUPPLIER") }}</div>
        <br /><br />
      </b-row>
      <b-row>
        <b-col xl="5">
          <b-row class="d-flex align-items-center mb-1">
            <b-col xl="4">
              <label class="highLightHeaderLabel">{{
                $t("SUPPLIERNAME")
              }}</label>
            </b-col>
            <b-col>
              <b-form-input
                v-model="supplierInfo.supplierName"
                class="form-control"
                disabled
              ></b-form-input>
                <show-errors
                :control="$v.supplierInfo.supplierName"
                :field="$t('SUPPLIERNAME')"
              />
            </b-col>
          </b-row>

          <b-row class="d-flex align-items-center mb-1">
            <b-col xl="4">
              <label class="highLightHeaderLabel">{{
                $t("REDUCTIONPROPOSALMENUNAME")
              }}</label>
            </b-col>
            <b-col>
              <b-form-input
                v-model="supplierInfo.reductionProposalName"
                class="form-control"
                disabled
              ></b-form-input>
              <show-errors
                :control="$v.supplierInfo.reductionProposalName"
                :field="$t('REDUCTIONPROPOSALMENUNAME')"
              />
            </b-col>
          </b-row>

          <b-row class="d-flex align-items-center mb-1">
            <b-col xl="4">
              <label class="highLightHeaderLabel">{{ $t("ACCOUNT") }}</label>
            </b-col>
            <b-col>
              <b-form-input
                v-model="supplierInfo.accountName"
                class="form-control"
                disabled
              ></b-form-input>
              <show-errors
                :control="$v.supplierInfo.accountName"
                :field="$t('ACCOUNT')"
              />
            </b-col>
          </b-row>

          <b-row class="d-flex align-items-center mb-1">
            <b-col xl="4">
              <label class="highLightHeaderLabel">{{
                $t("EXPENSEITEM")
              }}</label>
            </b-col>
            <b-col>
              <b-form-input
                v-model="supplierInfo.expenseItemName"
                class="form-control"
                disabled
              ></b-form-input>
              <show-errors
                :control="$v.supplierInfo.expenseItemName"
                :field="$t('EXPENSEITEM')"
              />
            </b-col>
          </b-row>
          <br />
        </b-col>
        <b-col xl="5">
          <b-button
            @click="showProposalModal"
            :disabled="reductionButton"
            variant="primary"
            class="align-content-center doubleHeight-btnAny"
            >{{ $t("SELECTREDUCTIONPROPOSALMENUBTN") }}</b-button
          >
        </b-col>
      </b-row>
      <br />
    </div>
    <br />
    <div class="border border-dark pl-4 pr-4 ml-5">
      <b-row>
        <div class="title-fs-2">{{ $t("CONTRACTINFO") }}</div>
        <br /><br />
      </b-row>
      <b-row>
        <b-col xl="5">
          <b-row class="d-flex align-items-center">
            <b-col xl="4">
              <label class="highLightHeaderLabel">{{
                $t("APPLICATIONDATE")
              }}</label>
            </b-col>
            <b-col>
              <b-input-group>
                <b-form-input
                  v-model="applicationDate"
                  type="text"
                  autocomplete="off"
                ></b-form-input>
                <b-input-group-append>
                  <b-form-datepicker
                    @context="onContext"
                    v-model="applicationDate"
                    button-only
                    right
                  ></b-form-datepicker>
                </b-input-group-append>
              </b-input-group>
            </b-col>
          </b-row>
          <br />
          <b-row class="d-flex align-items-center">
            <b-col xl="4">
              <label class="highLightHeaderLabel">{{
                $t("CONTRACTSTARTDATE")
              }}</label>
            </b-col>
            <b-col>
              <b-input-group>
                <b-form-input
                  v-model="contractstartDate"
                  type="text"
                  autocomplete="off"
                ></b-form-input>
                <b-input-group-append>
                  <b-form-datepicker
                    v-model="contractstartDate"
                    @context="onContext"
                    button-only
                    right
                  ></b-form-datepicker>
                </b-input-group-append>
              </b-input-group>
            </b-col>
          </b-row>
        </b-col>
        <b-col xl="7">
          <b-row class="d-flex align-items-center">
            <b-col xl="3">
              <label class="highLightHeaderLabel">{{
                $t("CONTRACTSTATUSSELECT")
              }}</label>
            </b-col>
            <b-col>
              <b-form-select
                :plain="true"
                v-model="selectedStaus"
                :options="situationOptions"
                value-field="item"
                text-field="name"
                class="customselect"
              ></b-form-select>
            </b-col>
            <b-col>
              <b-button variant="primary">{{
                $t("CONTRACTSTATUSEDIT")
              }}</b-button>
            </b-col>
          </b-row>
          <br />
          <b-row class="d-flex align-items-center">
            <b-col xl="3">
              <label class="highLightHeaderLabel">{{ $t("CONTRACTOR") }}</label>
            </b-col>
            <b-col>
              <b-form-input
                v-model="contractPersonIncharge"
                class="form-control"
                disabled
              ></b-form-input>
            </b-col>
            <b-col>
              <b-button @click="showPersonSelect" variant="primary">{{
                $t("SELECTCONTACT")
              }}</b-button>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
      <br />
    </div>
    <br />
    <b-button
      @click="registerData"
      :disabled="reductionButton"
      variant="primary"
      class="px-4 align-content-center ml-5"
      >{{ $t("CONTRACTINFORMATIONREGISTRATION")
      }}<i v-if="isBusy" class="fa fa-spinner fa-spin"
    /></b-button>
    <br /><br /><br />
    <b-modal
      id="person-modal"
      ref="person-modal"
      :hide-footer="true"
      centered
      variant="secondary"
      modal-class="py-0"
      body-class="py-0 "
      size="md"
      footer-class="border-0"
      header-class="border-0 bg-white pd-0"
      button-size="sm"
      hide-header-close
    >
      <template v-slot:modal-header="{ cancel }">
        <div class="title-fs font-weight-bold mt-2 text-dark ml-3">
          {{ $t("SELECTCONTACT") }}
        </div>
        <b-button
          @click="cancel"
          class="back-btn close-btn mr-3 mt-2"
          variant="primary"
          >{{ $t("CLOSE") }}</b-button
        >
      </template>
      <b-card class="pop-up-box">
        <b-row>
          <b-col>
            <b-form-group>
              <b-form-radio-group
                id="radio-group-1"
                v-model="personSelect"
                :options="personOptions"
                class="radioControl mb-3 pt-2"
                size="md"
                stacked
              ></b-form-radio-group>
            </b-form-group>
          </b-col>
        </b-row>
      </b-card>
      <b-row>
        <b-col cols="12" class="text-center mb-2">
          <b-button @click="getPerson" variant="primary" class="float-right"
            >{{ $t("ADD") }}
          </b-button>
        </b-col>
      </b-row>
    </b-modal>
    <div>
      <b-modal
        id="proposal-modal"
        ref="proposal-modal"
        :hide-footer="true"
        centered
        variant="secondary"
        modal-class="py-0"
        body-class="py-0 mb-3 modal-bodyAlign"
        size="lg"
        footer-class="border-0"
        header-class="border-0 bg-white"
        button-size="sm"
      >
        <template v-slot:modal-header="{ cancel }">
          <h5 class="title-fs font-weight-bold mt-2 text-dark ml-3">
            {{ $t("COSTPROPOSALSELECTION") }}
          </h5>
          <b-button
            @click="cancel"
            class="back-btn close-btn mt-2"
            variant="primary"
            >{{ $t("CLOSE") }}</b-button
          >
        </template>
        <hr class="mt-0 mb-2" />
        <div class="margin-b-1 mt-1 attachment-card">
          <div>
            <b-form inline>
              <b-row>
                <b-col cols="12" xl="12">
                  <span class="inlineLabel"
                    >&nbsp;{{ $t("REGISTRATIONFEE") }}</span
                  >
                </b-col>
              </b-row>
              <b-row>
                <b-col>
                  <b-form-select
                    @change="onExpenseChange($event)"
                    v-model="expense_code"
                    :options="expenseItemOptions"
                    :plain="true"
                    class="w-29"
                    size="sm"
                  ></b-form-select
                ></b-col>
              </b-row>
            </b-form>
          </div>
          <b-row class="mt-2 mb-2">
            <b-col xl="12">
              <l-table
                @onChecked="selectItem"
                :hasPagination="false"
                :showPerPage="false"
                :isBusy="isBusy"
                :table-data="tableData"
                :fields="fields"
                :outlined="true"
                :per-page="limit"
                small="small"
                pagination-align="center"
                fixed
                sticky-header
              >
              </l-table>
            </b-col>
          </b-row>
        </div>
        <hr class="mt-0 mb-0" />
        <div class="btn-style1">
          <b-button
            @click="submitChoice"
            :disabled="checkedItems == ''"
            type="submit"
            variant="primary"
            class="float-right send-btn mt-2 m-bodyAlign"
            >{{ $t("SELECTEXPENSEITEMS") }}
            <i v-if="isBusy" class="fa fa-spinner fa-spin" />
          </b-button>
        </div>
      </b-modal>
    </div>
  </div>
</template>

<script>
import {required} from "vuelidate/lib/validators";
import { Settings } from "luxon";
Settings.defaultLocale = "ja";
import { mapGetters } from "vuex";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { getDate } from "@/shared/utils";
import LTable from "@/components/ListTable";
import { endPoint } from "@/shared/endPoint";
import ShowErrors from "@/components/ShowErrors";
import { getDateHourMinute } from "@/shared/utils";
export default {
  components: { LTable,ShowErrors },
  data() {
    return {
      supplierInfo: {
        supplierName: "",
        reductionProposalName: "",
        accountName: "",
        expenseItemName: "",
      },
      applicationDate: "",
      contractstartDate: "",
      formatted: "",
      selected: "",
      contactStatus: "",
      contractPersonIncharge: "",
      personSelect: "",
      personOptions: [],
      tableData: [],
      checkedItems: "",
      isBusy: false,
      expense_code: { expense_item_cd: "", accounts_name_cd: "" },
      expenseItemOptions: [],
      selectedItem: "",
      supplierReductionProposalInfoListObject: [],
      personId: "",
      selectedPerson: {},
      situationOptions: [],
      selectedStaus: "",
      aggrementData: {},
      personData: [],
      limit: 0,
      reductionButton: false,
      personIncharge:""
    };
  },
    validations: {
    supplierInfo: {
      supplierName: {required},
      reductionProposalName: {required},
      accountName: {required},
      expenseItemName: {required},
    },
    },
  computed: {
    ...mapGetters({
      client_details: "getClientData",
      PayLoad: "getAggrementNo",
    }),
    fields() {
      return [
        {
          key: "checked",
          label: "",
          class: "text-center normal-width",
        },
        {
          key: "expense_item_name",
          label: this.$t("EXPENSEITEM"),
          class: "text-center normal-width",
        },
        {
          key: "reduction_proposal_name",
          label: this.$t("REDUCTIONPROPOSALMENUNAME"),
          class: "text-center normal-width",
        },

        {
          key: "created_date",
          label: this.$t("EXPIRATIONDATESTART"),
          class: "text-center normal-width",
        },
        {
          key: "updated_date",
          label: this.$t("EXPIRATIONDATEEND"),
          class: "text-center  normal-width",
        },
        {
          key: "reduction_proposal_cost",
          label: this.$t("PROPOSEDAMOUNT"),
          class: "text-center normal-width",
        },
      ];
    },
  },
  mounted() {
    this.getSupllierData();
    this.checkDate();
    this.getSupplierInfoData();
    console.log("p_id",this.aggrementData.agreement_person_id)
  },
  methods: {
    /**
     * @description function to validate the login person is available in suplierInfo data
     * @name getSupplierInfoData
     */
    async getSupplierInfoData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATAMSUPPLIERPERSONINFO,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "person_id",
              value: localStorage.getItem("personID"),
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data;
        if (responseData.length < 1) {
          this.reductionButton = true;
        }
      }
    },
    /**
     * @description function to validate the applicationDate
     * @name checkDate
     */
    checkDate() {
      if (this.contractstartDate > this.applicationDate) {
        this.contractstartDate = this.contractstartDate;
      } else {
        this.contractstartDate = "";
      }
    },
    /**
     * @description function to display person popup-modal
     * @name showPersonSelect
     */
    showPersonSelect() {
      this.personSelect = this.personId;
      this.$refs["person-modal"].show();
    },
    /**
     * @description function to fetch the selected person in popup-modal
     * @name getPerson
     */
    getPerson() {
      this.selectedPerson = this._.find(this.personOptions, {
        value: this.personSelect,
      });
      this.contractPersonIncharge = this.selectedPerson.text;
      this.personId = this.selectedPerson.value;
      this.getStatusOnChange();
      this.$refs["person-modal"].hide();
    },
    /**
     * @description function to fetch the personData and add to the pop-up options
     * @name getPersonList
     */
    async getPersonList() {
      const { data } = await BasicInfoApis.postAPI(endPoint.MASTERREADMPERSON, {
        fieldlist: ["person_id", "person_name"],
      });
      if (data.success) {
        let responseData = data.data;
        this.personData = responseData;
        let listData = [];
        if (responseData.length) {
          responseData.forEach((item) => {
            let iteratePerson = {
              text: item.person_name ? item.person_name : "",
              value: item.person_id ? item.person_id : "",
            };
            listData.push(iteratePerson);
          });
          this.personOptions = listData;
        } else {
          this.personOptions = [];
        }
        if (this.PayLoad.flagcheck != false) {
          this.situationOptions = [];
          this.getStatus();
        }
      } else {
        this.personOptions = [];
      }
    },
    /**
     * @description function to validate the fetch the suplier data or not
     * @name getSupllierData
     */
    async getSupllierData() {
      this.getPersonList();
      if (this.PayLoad.flagcheck == false) {
        const { data } = await BasicInfoApis.postAPI(
          endPoint.CLIENTREADAGREEMENTDETAIL,
          {
            client_no: this.client_details.client_no,
            agreement_no: this.PayLoad.aggrementno,
          }
        );
        if (data.success) {
          let responseData = data.data.agreementObject;
          this.aggrementData = responseData;
          this.getStatusData();
          this.supplierInfo.supplierName = responseData.supplier_name;
          this.supplierInfo.reductionProposalName =
            responseData.reduction_proposal_name;
          this.supplierInfo.accountName = responseData.accounts_name_nm;
          this.supplierInfo.expenseItemName = responseData.expense_item_nm;
          this.personId = responseData.agreement_person_id;
          this.selectedStaus = responseData.agreement_status_no;
          if (responseData.person_name == null) {
            this.selected = "";
          } else {
            this.contractPersonIncharge = responseData.person_name;
          }
          this.applicationDate = responseData.agreement_request_date;
          this.contractstartDate = responseData.agreement_start_date;
          this.situationOptions = [];
        }
      }
    },
    /**
     * @description function to fetch the status based on the person change
     * @name getStatusOnChange
     */
    async getStatusOnChange() {
      this.situationOptions = [];
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADMAGREEMENTSTATUS,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "use_flg",
              value: true,
            },
            {
              key: "person_id",
              value: this.selectedPerson.value,
            },
          ],
        }
      );
      let responseData = data.data;
      for (let index = 0; index < responseData.length; index++) {
        this.situationOptions.push({
          item: responseData[index].agreement_status_no,
          name: responseData[index].agreement_status_name,
        });
      }
    },
    /**
     * @description function to fetch the status based on the supplier data person_id
     * @name getStatusData
     */
    async getStatusData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADMAGREEMENTSTATUS,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "use_flg",
              value: true,
            },
            {
              key: "person_id",
              value: this.aggrementData.agreement_person_id,
            },
          ],
        }
      );
      let responseData = data.data;
      for (let index = 0; index < responseData.length; index++) {
        this.situationOptions.push({
          item: responseData[index].agreement_status_no,
          name: responseData[index].agreement_status_name,
        });
      }
    },
    /**
     * @description function to fetch the status based on the login person
     * @name getStatus
     */
    async getStatus() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADMAGREEMENTSTATUS,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "use_flg",
              value: true,
            },
            {
              key: "person_id",
              value: localStorage.getItem("personID"),
            },
          ],
        }
      );
      let Data = data.data;
      let len = Data.length;
      for (let index = 0; index < len; index++) {
        this.situationOptions.push({
          item: Data[index].agreement_status_no,
          name: Data[index].agreement_status_name,
        });
      }
    },
    /**
     * @description function to validate the application date and contract date
     * @name onContext
     */
    onContext(ctx) {
      if (this.contractstartDate > this.applicationDate) {
        this.formatted = ctx.selectedFormatted;
        this.selected = ctx.selectedYMD;
      } else {
        this.contractstartDate = "";
      }
    },
    /**
     * @description function to show the popup-modal
     * @name showProposalModal
     */
    showProposalModal() {
      this.checkedItems = "";
      this.expense_code = { expense_item_cd: "", accounts_name_cd: "" };
      this.getExpenseData();
      this.getExpenseItemList();
      this.$refs["proposal-modal"].show();
    },
    /**
     * @description function to hide the popup-modal
     * @name hideMessageModal
     */
    hideMessageModal() {
      this.$refs["proposal-modal"].hide();
    },
    /**
     * @description function to fetch the checked items in popup-modal
     * @name selectItem
     */
    selectItem(items) {
      this.checkedItems = items;
    },
    /**
     * @description function to fetch the expenseData
     * @name onExpenseChange
     */
    onExpenseChange() {
      this.getExpenseData();
    },
    /**
     * @description function to fetch the expensedata based on person_id
     * @name getExpenseData
     */
    async getExpenseData() {
      this.isBusy = true;
      let compiledArray;
      if (this.expense_code.expense_item_cd == "") {
        compiledArray = {
          expense_item_cd: this.expense_code.expense_item_cd,
          accounts_name_cd: this.expense_code.accounts_name_cd,
          person_id: localStorage.getItem("personID"),
        };
      } else {
        compiledArray = {
          expense_item_cd: this.expense_code.expense_item_cd,
          accounts_name_cd: this.expense_code.accounts_name_cd,
          person_id: localStorage.getItem("personID"),
        };
      }
      const { data } = await BasicInfoApis.postAPI(
        endPoint.CLIENTREADSUPPLIERREDUCTIONPROPOSAL,
        compiledArray
      );
      this.isBusy = false;
      if (data.success) {
        let listObject = data.data.supplierReductionProposalInfoListObject;
        this.supplierReductionProposalInfoListObject = [
          ...data.data.supplierReductionProposalInfoListObject,
        ];
        let mapData = [];
        this.limit = listObject.length;
        listObject.forEach((item, i) => {
          let listData = {
            id: i,
            supplier_expense_item_info_no: item.supplier_expense_item_info_no,
            expense_item_name: item.expense_item_nm,
            reduction_proposal_name: item.reduction_proposal_name,
            reduction_proposal_cost: this.filterByCurrency(
              item.reduction_proposal_cost
            ),

            created_date: getDate(new Date(item.effective_date_st)),
            updated_date: getDate(new Date(item.effective_date_ed)),
          };
          mapData.push(listData);
        });
        this.tableData = mapData;
        this.checkedItems = "";
      }
    },
    /**
     * @description function to convert  the number to currency
     * @name getStatusOnChange
     */
    filterByCurrency(value) {
      try {
        return parseInt(value)
          .toString()
          .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
      } catch {
        return 0;
      }
    },
    /**
     * @description function to fetch the expenseItemsList Based on the person_id
     * @name getExpenseItemList
     */
    async getExpenseItemList() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.SUPPLIERREADSUPPLIEREXPENSEITEMLIST,
        {
          person_id: localStorage.getItem("personID"),
        }
      );
      if (data.success) {
        let responseData = data.data.expenseItemObjectList;
        let expenseListData = [];
        if (responseData.length) {
          responseData.forEach((item) => {
            let iterateExpenseItem = {
              text: item.expense_item_nm ? item.expense_item_nm : "",
              value: {
                expense_item_cd: item.expense_item_cd
                  ? item.expense_item_cd
                  : "",
                accounts_name_cd: item.accounts_name_cd
                  ? item.accounts_name_cd
                  : "",
              },
            };
            if (item.expense_item_nm) {
              expenseListData.push(iterateExpenseItem);
            }
          });
          this.expenseItemOptions = expenseListData;
        } else {
          this.expenseItemOptions = [];
        }
      } else {
        this.expenseItemOptions = [];
      }
    },
    /**
     * @description function to fetch the checked item on the popup modal on subiting the choice
     * @name submitChoice
     */
    submitChoice() {
      this.$refs["proposal-modal"].hide();
      const checkedItem = this.checkedItems.index;
      this.selectedItem = {
        ...this.supplierReductionProposalInfoListObject[checkedItem],
      };
      this.supplierInfo.supplierName = this.selectedItem.supplier_name;
      this.supplierInfo.reductionProposalName = this.selectedItem.reduction_proposal_name;
      this.supplierInfo.accountName = this.selectedItem.accounts_name_nm;
      this.supplierInfo.expenseItemName = this.selectedItem.expense_item_nm;
    },
    /**
     * @description function to check crate or update
     * @name registerData
     */
    registerData() {
      this.$v.supplierInfo.$touch();
      if (!this.$v.supplierInfo.$invalid) {
      if (this.PayLoad.flagcheck == false) {
        this.isBusy = true;
        this.updateData();
      } else {
        this.isBusy = true;
        this.createData();
      }
      }
    },
    /**
     * @description function to update the data
     * @name updateData
     */
    async updateData() {
     
      if (this.selectedPerson.value) {
        this.personIncharge = this.selectedPerson.value;
      } else {
        this.personIncharge = this.personId;
      }

      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERUPDATETAGREEMENT,
        {
          data: [
            {
              supplier_no: this.selectedItem.supplier_no,
              accounts_name_cd: this.selectedItem.accounts_name_cd,
              expense_item_cd: this.selectedItem.expense_item_cd,
              agreement_request_date: this.applicationDate,
              agreement_start_date: this.contractstartDate,
              agreement_status_no: this.selectedStaus,
              agreement_person_id:this. personIncharge,
              create_id: this.client_details.client_no,
              update_id: this.client_details.client_no,
              supplier_reduction_proposal_info_no: this.selectedItem
                .supplier_reduction_proposal_info_no,
            },
          ],
          conditions: [
            {
              key: "agreement_no",
              value: this.PayLoad.aggrementno,
            },
          ],
        }
      );
      this.isBusy = false;
      if(data.success)
      {
        this.updateTaskData()
      }
 
    },
    async updateTaskData()
    {
     const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERUPDATETTASK,
        {
          data: [
            {
            "task_person_id": this.personIncharge,
            "task_dt": getDateHourMinute(new Date()),
            "del_flg": false,
            "create_id": this.personIncharge,
            "update_id": this.personIncharge,
            },
          ],
          conditions: [
            {
              key: "task_body_no",
              value: this.PayLoad.aggrementno,
            },
               {
              key: "task_type",
              value: "002",
            },
          ],
        }
      );
           
        if (data.success) {
        if (this.PayLoad.page) {
          this.$router.push({ name: "client_mgmt_request_app_detail" });
        } else {
          this.$router.push({ name: "client_process" });
        }
      }
    },
    /**
     * @description function to createData
     * @name CreateData
     */
    async createData() {
      let reduction_proposal_no = this.PayLoad.reduction_proposal_no;
    this. personIncharge;
      if (this.selectedPerson.value) {
       this. personIncharge = this.selectedPerson.value;
      } else {
       this. personIncharge = localStorage.getItem("personID");
      }
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERCREATETAGREEMENT,
        [
          {
            client_no: this.client_details.client_no,
            supplier_no: this.selectedItem.supplier_no,
            accounts_name_cd: this.selectedItem.accounts_name_cd,
            expense_item_cd: this.selectedItem.expense_item_cd,
            agreement_request_date: this.applicationDate,
            agreement_start_date: this.contractstartDate,
            agreement_person_id: this.personIncharge,
            agreement_status_no: this.selectedStaus,
            create_id: this.client_details.client_no,
            update_id: this.client_details.client_no,
            supplier_reduction_proposal_info_no: this.selectedItem
              .supplier_reduction_proposal_info_no,
            reduction_proposal_no: reduction_proposal_no,
          },
        ]
      );
      this.isBusy = false;
      if(data.success)
      {
        let responseData=data.data
        this.registerTaskData(responseData)
      }
    },
   async registerTaskData(agrementNo)
    {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERCREATETTASK,
        [
          {
            "task_type": "002",
            "task_body_no":parseInt(agrementNo),
            "task_kbn": "01",
            "task_person_id": this.personIncharge,
            "task_dt": getDateHourMinute(new Date()),
            "del_flg": false,
            "create_id": this.personIncharge,
            "update_id": this.personIncharge,
          },
        ]
      );
      this.isBusy = false;
      if(data.success)
      {
          if (this.PayLoad.page) {
          this.$router.push({ name: "client_mgmt_request_app_detail" });
        } else {
          this.$router.push({ name: "client_process" });
        }
      }
    }
  },
};
</script>

